import React, {useState} from 'react';
import {View, Text, Image, TouchableOpacity} from 'react-native';

import DashboardHeader from './DashboardHeader';
import BackArrow from './BackArrow';
import sharedStyles from '../../Styles/Shared/style';


import SettingsIcon from '../../assets/icons/Settings.png';


import ManageBranchesIcon from '../../assets/icons/manage_branches.png';
import ManageOrdersIcon from '../../assets/icons/manage_orders.png';
import ManageAccountIcon from '../../assets/icons/manage_account.png';

import {InfoModal} from '../Modals/InfoModal';


export function Manage(props){

    const [InfoModalState, setInfoModalState]             = useState(false);
    const [infoMsg, setInfoMsg]                           = useState("");


    const manageCreatedOrders = () =>{
        props.navigation.navigate('CreatedOrders');
    }

    const manageAccount = () =>{
        props.navigation.navigate('Profile');
    }

    const manageBranchesAccounts = () =>{
        setInfoMsg("     !");
        setInfoModalState(true);
    }


    return(
        <View>
            <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} />

            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>

       

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View>

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />
                            <Text style={sharedStyles.viewsTitleText}></Text>
                            <Image style={{width: 30, height: 30, marginRight: 20}} source={SettingsIcon} />

                        </View>
                    </View>
                </View>


            </View>



            
            {/* <View style={{width: '80%', flexGrow: 1, flexDirection: 'column', justifyContent: 'center', alignSelf: 'center', marginTop: 20}}> */}
                
            <View style={{flexGrow: 1, flexDirection: 'column', justifyContent: 'space-between', padding: 20}}>

                <View style={{width: "90%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-evenly', marginLeft: 50, marginRight: 50, padding: 10}}>

                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{manageCreatedOrders()}}>
                            <Image source={ManageOrdersIcon} style={{width:79.5, height:67}} />
                            <Text style={sharedStyles.DashCardText}>  </Text>
                        </TouchableOpacity>
                    </View>


                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{manageAccount()}}>
                            <Image source={ManageAccountIcon} style={{width:65, height:62.5}} />
                            <Text style={sharedStyles.DashCardText}> </Text>
                        </TouchableOpacity>
                    </View>

                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{manageBranchesAccounts()}}>
                            <Image source={ManageBranchesIcon} style={{width:75, height:70.5}} />
                            <Text style={sharedStyles.DashCardText}>  </Text>
                        </TouchableOpacity>
                    </View>


                </View>

            </View>



            {/* </View> */}
            
        </View>
    );

}



export default Manage;
