
import React from 'react';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import rootReducer from './Views/reducers';
import {Platform, View, Text} from 'react-native';
import {isMobile} from 'react-device-detect';

import Logo from './assets/images/trainee_ar.png';


import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';

import * as Font from 'expo-font';

import Login from './Views/Login';

import SignUp from './Views/SignUp';
import ForgotPassword1 from './Views/ForgotPassword1';
import ForgotPassword2 from './Views/ForgotPassword2';
import Dashboard from  './Views/Dashboard';
import Notifications from './Views/Components/Notifications';
import NotificationDetails from './Views/Components/NotificationDetails';
import CreateOrder from './Views/CreateOrder';
import Logs from './Views/Components/Logs';
import Manage from './Views/Components/Manage';
import CreatedOrders from './Views/CreatedOrders';
import EditOrder from './Views/Components/EditOrder';
import Profile from './Views/Profile';
// import TraineeRequests from './Views/Components/TraineeRequests';

import { Image } from 'react-native-web';


const customFonts        = {
  'Arslan': require('./assets/fonts/Arslan.ttf'),
};

const Stack              = createNativeStackNavigator();

const store              = createStore(rootReducer, applyMiddleware(thunk));//composeWithDevTools()

export const Main = () =>{
    Font.loadAsync(customFonts);
    
    return (
      <NavigationContainer>
          <Stack.Navigator screenOptions={{headerShown: false, contentStyle: {backgroundColor: '#f8f8f8',}}}>

            <Stack.Screen name="Login" component={Login}/> 
  
            <Stack.Screen name="SignUp" component={SignUp} />
  
            <Stack.Screen name="ForgotPassword Step 1" component={ForgotPassword1} />
  
            <Stack.Screen name="ForgotPassword Step 2" component={ForgotPassword2} />
  
            <Stack.Screen name="Dashboard" component={Dashboard} />

            <Stack.Screen name="NotificationsDetails" component={NotificationDetails} />
            
            <Stack.Screen name="Notifications" component={Notifications} />

            <Stack.Screen name="Logs" component={Logs} />

            <Stack.Screen name="CreateOrder" component={CreateOrder} />

            <Stack.Screen name="Manage" component={Manage} />

            <Stack.Screen name="CreatedOrders" component={CreatedOrders} /> 

            <Stack.Screen name="EditOrder" component={EditOrder} />

            <Stack.Screen name="Profile" component={Profile} />

            {/* <Stack.Screen name="TraineeRequests" component={TraineeRequests} /> */}
            
  
          </Stack.Navigator>
      </NavigationContainer>
    );
}


const  Sorry = () => {
  Font.loadAsync(customFonts);

  return(

    <View style={{width: "100%", height: "100%", felx: 1, justifyContent: 'center', alignItems: 'center'}}>
        <Image source={Logo} style={{width: 400, height: 300, alignSelf: 'center'}} />
        <Text style={{fontFamily: 'Arslan', textAlign: 'center', fontSize: 30, fontWeight: 'bold', color: '#EF8630'}}>
                 
        </Text>
        <Text style={{fontFamily: 'Arslan', textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: 'red'}}>
                          
        </Text>
        <Text style={{fontFamily: 'Arslan', textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: 'black'}}>
                             
        </Text>

        <Text style={{fontFamily: 'Arslan', marginTop: 30, textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: '#EF8630'}}>
         | 2022
        </Text>
        <Text style={{fontFamily: 'Arslan', marginTop: 5, textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: '#EF8630'}}>
          Trainee-Platform      
        </Text>
    </View>

  );
}

export default function App (){
  return(
    <Provider store={store}>
      {
        isMobile ?
          <Sorry />
        :
          <Main />
      }
    </Provider>
  );
}