import cryptoJs from "crypto-js";

import Cookies from 'universal-cookie/es6';

// export const uri        = "http://127.0.0.1:8000";

export const uri        = "https://tpteam1.pythonanywhere.com";

var firstLaunchVar      = false;

var decrypted_Token     = "";

var decrypted_username  = "";

var institution_details = "";

var noitificationCardId = null;

var logoUri             = "";

