import { Modal, View, Text, Pressable } from 'react-native';

import {useNavigation} from '@react-navigation/native';

import styles from '../../Styles/Modals/style'

export const InfoModal = (props) => {

    const navigation = useNavigation(); 

    let goToDashboardAfterClose = false;  

    const title = "\n! ";

    var titleInfo = null;

    if(props.message.length > 0) {
        if(props.message.includes("")){
          titleInfo = styles.titleInfo2;
          goToDashboardAfterClose = true; 
        }else{
          titleInfo = styles.titleInfo;
        }
    }

    const closeModal = () => {
      if(props.doubleClose === true){
        props.setParentModalVisible(false);
      }
      if(props.navigateTo === true){
        console.log("here 2");
        navigation.navigate(props.distinationAfterClose);  
      }

      props.setModalVisible(false);

      if(goToDashboardAfterClose){
        if(props.setGoToDashboard !== undefined){
          props.setGoToDashboard(true)
        }
      } 
    }
    

    return(

        <Modal 
            animationType="fade"
            transparent={true}
            visible={props.modalVisible}>


            <View style={styles.centeredView}>
              <View style={styles.backGroundCard}>
                <View style={styles.modalView}>

                  <Text style={titleInfo}>{title}</Text>

                  <View style={styles.textView}>

                      <Text style={styles.modalText}>
                      {props.message}
                      </Text>
                      
                      <Pressable
                          style={[styles.button, styles.buttonClose]}
                          onPress={closeModal}>
                          <Text style={styles.textStyle}></Text>
                      </Pressable>

                  </View>
                </View>
              </View>
            </View>


        </Modal>

    );

}

