import {uri} from '../Globals/index';


export const fetchNewToken =  (email, password) => {
    let data          = [];
    return async (dispatch) => {

        var formdata = new FormData();
        formdata.append("email", email);
        formdata.append("password", password);

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/login/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                let parsed_data = await response.json();
                data = parsed_data;
                data["status"]      = response.status;
                data["responseMsg"] = "";
            }else if(response.status === 423){
                data["status"]      = response.status;
                data["responseMsg"] = "";
            }else if(response.status === 451){
                data["status"]      = response.status;
                data["responseMsg"] = `
                            \n   
                            \n        
                `;
            }else if(response.status === 401){
                data["status"]      = response.status;
                data["responseMsg"] = "      ";
            }
        }).catch(error => console.log('error', error));

        dispatch({
            type: 'token',
            payload: data
        });
    };

}


