export const registerInstitution = (name, username, email, phone, password, address, taxID, domainType) => {
    let data          = [];
    return async (dispatch) => {

        var formdata = new FormData();
        formdata.append("name", name);
        formdata.append("username", username);
        formdata.append("email", email);
        formdata.append("phone", phone);
        formdata.append("location", address);
        formdata.append("taxID", taxID);
        formdata.append("domainType", domainType);
        formdata.append("password", password);

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/register/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                data["status"] = 200;
            }else if(response.status === 422){
                data["status"] = 422;
                data["responseMsg"] = "        ";
            }else if(response.status === 400){
                let parsed_data = await response.json();
                data["responseMsg"] = parsed_data;
            }
        }).catch(error => console.log('error', error));

        dispatch({
            type: 'registeration',
            payload: data
        });
    };

}


export const confirmAccount =  (editCode) => {
    let data          = [];
    return async (dispatch) => {

        var formdata = new FormData();
        formdata.append("editCode", editCode);

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/activate_account/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                let isAccountActive = await response.json();
                isAccountActive = isAccountActive.is_active;


                data["isAccountActive"] = isAccountActive
                data["status"]      = 200;

                if(isAccountActive){
                    data["responseMsg"] = `
                        \n     
                        \n    
                        \n\n\n(       )
                    `;
                }else{
                    data["responseMsg"] = `
                        \n!    
                         \n    
                         \n    
                         \n    
                    `;
                }
            }else if(response.status === 400){
                data["status"]      = 400;
                data["responseMsg"] = "  ";
            }
        }).catch(error => console.log('error', error));

        dispatch({
            type: 'account_confirmation',
            payload: data
        });
    };

}

