export const sendCreateOrderRequest =  (username, token, title, poster=null, description, expireDate, availableSeats, maxSubmits) => {
    let data          = [];

    return async (dispatch) => {    
        var formdata = new FormData();
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        formdata.append("username", username);
        formdata.append("title", title);

        if(poster !== null){
            formdata.append("poster", poster, poster.name);
        }

        formdata.append("orderDetails", description);
        formdata.append("expireDate", expireDate);
        formdata.append("availableSeats", availableSeats);
        formdata.append("max_submits", maxSubmits);

        var requestOptions = {
            method: 'POST',
            headers: headerData,
            body: formdata,
            redirect: 'follow'
        };


        try{

            const controller = new AbortController();
            const timeout    = 6000;
            const id         = setTimeout(() => {
                controller.abort();
                data["status"]      = 408;
                data["responseMsg"] = "  !";
                dispatch({
                    type: 'sendCreateOrderRequestAction',
                    payload: data
                });
            }, timeout);

            const response = await fetch(`${uri}/api/institution/profile/${username}/create_order/`, requestOptions, {signal: controller.signal})
            .then( async (response) => {
                if(response.status === 200){
                    data["status"]      = 200;
                    data["responseMsg"] = "    !";
                }else if(response.status === 400) {
                    let parsed          = response.json();
                    data["status"]      = 400;
                    data["responseMsg"] = parsed;
                }else if(response.status === 404){
                    data["status"]      = 404;
                    data["responseMsg"] = "   !";
                }
            }).catch(err => {console.log(err)});

            clearTimeout(id);

            dispatch({
                type: 'sendCreateOrderRequestAction',
                payload: data
            });

        }catch(error){
            console.log('error', error.message);
        }

    };



}


