import {View, Text, Image, TouchableOpacity} from 'react-native';
import { useNavigation, StackActions } from '@react-navigation/native';


import sharedStyles from '../../Styles/Shared/style';


import backArrowIcon from '../../assets/icons/back_arrow.png';

export default function BackArrow(props){
    const navigation = useNavigation();



    const handleBackEvent = () => {
        navigation.goBack();
    }


    return (
        <View>
            <View style={sharedStyles.arrowBackbuttonBackLayer}>
                <TouchableOpacity style={sharedStyles.arrowBackbuttonFrontLayer} onPress={()=>{handleBackEvent()}}>
                    <Image source={backArrowIcon} style={sharedStyles.arrowBtnIcon} />
                    <Text style={sharedStyles.textBtn}>  </Text>
                </TouchableOpacity>
            </View>
        </View>
    );
}
