const rootReducer = combineReducers({
    token_response_data,
    InstitutionDetails,
    InstitutionRegisteration,
    AccountConfirmation,
    ForgotPassword1ActionResponse,
    ForgotPassword2ActionResponse,
    NotificationsResponse,
    NotificationResponse,
    getCreateOrderResponse,
    getSaveOrderResponse,
    getOrdersResponse,
    getUpdateOrderResponse,

})

export default rootReducer;