import React, {useState, useEffect} from 'react';

import {View, Text, Image} from 'react-native';

import {connect} from 'react-redux';

import {getIdForNotificationCard, getDecryptedToken, getDecryptedUsername} from '../Globals/index';

import {useNavigation} from '@react-navigation/native';

import DashboardHeader from './DashboardHeader';
import BackArrow from './BackArrow';

import sharedStyles from '../../Styles/Shared/style';

import notifications_icon from '../../assets/icons/notifications.png';

import {fetchSingleNotification} from '../actions/index';

export function NotificationDetails(props) {

    const navigation            = useNavigation();

    const [title, setTitle]     = useState("");
    const [details, setDetails] = useState("");

    const updateState = () => {
        if(props.state.data !== ''){
            console.log(props.state.data);
            setTitle(props.state.data.notification.title);
            setDetails(props.state.data.notification.details);
        }
    }

    useEffect(() => {
        updateState();
    });

    return(
        <View>
            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>
                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow/>
                </View>

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />   
                            <Text style={sharedStyles.viewsTitleText}> </Text>
                            <Image style={{width: 30, height: 34, marginRight: 20}} source={notifications_icon} />

                        </View>
                    </View>
                </View>

            </View>


            <View style={{width: '80%', flexGrow: 1, flexDirection: 'column', justifyContent: 'center', alignSelf: 'center', marginTop: 20}}>

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>

                        <Text style={sharedStyles.NotificationCardTitleText}>{title}</Text>
                        <Text style={sharedStyles.NotificationCardSubText}>{details}</Text>

                    </View>
                </View>

            </View>


        </View>
    )

}


const mapStateToProps = (state) => {
    return {
        state: {
            data: state.NotificationResponse
        }
    }
}

export default connect(mapStateToProps, null)(NotificationDetails);