import React, {useState, useEffect, useRef} from 'react';
import {View, Text, Image, TouchableOpacity, TextInput} from 'react-native';

import {WaitingModal} from './Modals/WaitingModal';
import {InfoModal} from './Modals/InfoModal';
import ConfirmationModal from './Modals/ConfirmationModal';

import Header from './Components/Header';
import Footer from './Components/LoginFooter';


import loginStyles from '../Styles/Login/style';
import sharedStyles from '../Styles/Shared/style';


import emailIcon from '../assets/icons/signup_email.png';
import keyIcon from '../assets/icons/login_key2.png';
import showPasswordIcon from '../assets/icons/signup_opendEye.png';
import hidePasswordIcon from '../assets/icons/signup_closedEye.png';
import unlockIcon from '../assets/icons/login_unlock2.png';

import { fetchNewToken, fetchInstitutionDetails } from './actions/index';
import { connect } from 'react-redux';

import {handleFirstLaunch, encryptToken, decryptToken, 
        setDecryptedToken, setDecryptedUsername, getDecryptedToken } from './Globals/';

import Cookies from 'universal-cookie/es6';



export function Login(props){

    const [waitingModalVisible, setWaitingModalVisible]           = useState(false);
    const [confirmationModalVisible, setConfirmationModalVisible] = useState(false);
    const [infoModalVisible, setInfoModalVisible]                 = useState(false);
    const [infoModalMessage, setInfoModalMessage]                 = useState("");

    const [email, setEmail]                                       = useState("");
    const [password, setPassword]                                 = useState("");
    const [token, setToken]                                       = useState("");
    const [showPassword, setShowPassword]                         = useState(false);
    const [isEmailValid, setIsEmailValid]                         = useState(false);
    const [responseMsg, setResponseMsg]                           = useState("");
    
    const updateStates = () => {
        const cookie   = new Cookies();

        if(props.state.data !== undefined) {
            setWaitingModalVisible(false);

            if(props.state.data.token_response_data.status === 401){
                props.state.data.token_response_data.status     = "";
                setInfoModalMessage(props.state.data.token_response_data.responseMsg);
                setInfoModalVisible(true);

            }else if(props.state.data.token_response_data.status === 423){
                props.state.data.token_response_data.status = "";
                setConfirmationModalVisible(true);

            }else if(props.state.data.token_response_data.status === 451){
                props.state.data.token_response_data.status = "";
                setInfoModalMessage(props.state.data.token_response_data.responseMsg);
                setInfoModalVisible(true);
            }
        }

        if(props.state.data.token_response_data.username !== undefined) {
            if(props.state.data.token_response_data.token !== "" && cookie.get("trainee-cookie") === undefined && getDecryptedToken().length === 0) {
                
                const today    = new Date();
                const tomorrow = new Date(today);
                tomorrow.setDate(tomorrow.getDate() + 1);

                const username_token = props.state.data.token_response_data.username + "," + props.state.data.token_response_data.token

                const encrypted      = encryptToken(username_token);
                
                const options        = {
                    expires: tomorrow
                };
                
                cookie.set("trainee-cookie", encrypted, options);
                cookie.set("TraineeWelcomeMessage", true);

                setPassword("");

                setDecryptedToken(props.state.data.token_response_data.token);
                setDecryptedUsername(props.state.data.token_response_data.username);

                props.fetchInstitutionDetails(props.state.data.token_response_data.username, 
                    props.state.data.token_response_data.token);

                props.state.data.token_response_data.username = "";
                props.state.data.token_response_data.token    = "";
            }

            if(props.state.data.responseMsg !== "" ){
                setResponseMsg(props.state.data.responseMsg);
            }
        }

    }

    const controlDashboardTransition = () => {
        if(getDecryptedToken() !== undefined){
            if(getDecryptedToken().length > 0){
                props.navigation.navigate('Dashboard');
            }
        }
    }

    useEffect(()=>{
        updateStates();
        controlDashboardTransition();
    })



    const changeShowPasswordState = () => {
        setShowPassword(!showPassword);
    }

    const emailHandler = (email) => {
        setEmail(email);
    }

    const passwordHandler = (password) => {
        setPassword(password);
    }

    const loginInstitution = () => {
        if(!email.match("[a-zA-Z0-9._][@][a-zA-Z.]+[.][a-zA-Z]+")){
            setInfoModalMessage("    ! ");
            setInfoModalVisible(true);
        }else if (password.length < 8){
            setInfoModalMessage("          ! ");
            setInfoModalVisible(true);
        }else{
            setWaitingModalVisible(true);
            props.fetchNewToken(email, password);
        }
    }

    const handleCookie = () => {
        const cookies = new Cookies();
        if (cookies.get('trainee-cookie') !== undefined){
            const cookie = cookies.get('trainee-cookie');
            const decrypted  = decryptToken(cookie);
            let username_token = [];
            username_token = decrypted.split(",");
            setDecryptedUsername(username_token[0]);
            setDecryptedToken(username_token[1]);
            if(username_token[0] !== undefined){
                props.fetchInstitutionDetails(username_token[0], username_token[1]);
            }
        }
    }


    return(

        <View style={loginStyles.view}>
        {
            handleFirstLaunch() ?
                handleCookie()
            :
                null
        }

        {
            waitingModalVisible === true ?
                <WaitingModal />
            :
                null
        }

        {
            infoModalVisible === true ?
                <InfoModal message={infoModalMessage} setModalVisible={setInfoModalVisible}/>
            :
                null
        }
        
        {/* header */}
        <View style={{width: '100%'}}>
            <Header />
        </View>

        {
            confirmationModalVisible === true ?
                <ConfirmationModal  setModalVisible={setConfirmationModalVisible} />
            :
                null
        }


        {/* card */}
        <View style={loginStyles.cardBackLayer}>
            <View style={loginStyles.cardFrontLayer}>

            {/* title */}
            <Text style={sharedStyles.titleText}> </Text>
            {/* body */}
            <View style={loginStyles.body}>

                {/* username text input */}
                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <TextInput placeholder=" " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                            autoCapitalize={'none'} autoComplete={'off'} value={email} onChangeText={(value)=>{emailHandler(value)}} />
                        <Image source={emailIcon} style={sharedStyles.icon} />
                    </View>
                </View>

                {/* password text input */}
                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>

                        {
                            showPassword ?
                                <TouchableOpacity onPress={()=>{changeShowPasswordState()}}>
                                    <Image source={showPasswordIcon} style={sharedStyles.iconRight} />
                                </TouchableOpacity>
                            :
                                <TouchableOpacity onPress={()=>{changeShowPasswordState()}}>
                                    <Image source={hidePasswordIcon} style={sharedStyles.iconRight} />
                                </TouchableOpacity>
                        }
                        
                        <TextInput placeholder=" " placeholderTextColor={'#C7C7CD'} 
                            style={sharedStyles.textInputLarge} autoCapitalize={'none'}
                            secureTextEntry={!showPassword} value={password} 
                            autoComplete={'off'} onChangeText={(value)=>{passwordHandler(value)}} />
                        <Image source={keyIcon} style={sharedStyles.icon} />
                    </View>
                </View>



                {/* login btn */}
                <View style={{width: '65%', marginTop: 20}}>
                    <View style={sharedStyles.buttonBackLayer}>
                        <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{loginInstitution()}}>
                            <Text style={sharedStyles.textBtn}></Text>
                            <Image source={unlockIcon} style={sharedStyles.btnIcon} />
                        </TouchableOpacity>
                    </View>
                </View>

            </View>
            </View>
        </View>

        <View style={{width: '100%'}}>
            {/* footer */}
            <Footer/>
        </View> 

    </View>

    );
}



const mapStateToProps = (state) => {
    return {
        state: {
           data: state
        }
    }
}




const mapDispatchToProps = (dispatch) => {
    return{
        fetchNewToken: (email, password) => {
             dispatch(fetchNewToken(email, password)) 
        },

        fetchInstitutionDetails: (username, token) => {
            dispatch(fetchInstitutionDetails(username, token))
        },
    }
}



export default  connect(mapStateToProps, mapDispatchToProps)(Login);