import React, {useState, useEffect, setState} from 'react';
import {View, Text, Image, TextInput, TouchableOpacity} from 'react-native';

import {getDecryptedToken, getDecryptedUsername, uri, getIdForOrderCard} from './Globals/index';

import DashboardHeader from './Components/DashboardHeader';

import BackArrow from './Components/BackArrow';

import sharedStyles from '../Styles/Shared/style';

import orderIcon from '../assets/icons/order.png';

import OrderCard from './Components/OrderCard';

import "react-datepicker/dist/react-datepicker.css";

import {InfoModal} from './Modals/InfoModal';
import {WaitingModal} from './Modals/WaitingModal';



export function CreatedOrders(props){

    const [orders, setOrders] = useState(null);
    const [data, setData]     = useState(null);
    const [dataOnce, setDataOnce] = useState(false);


    const [waitingModalVisible, setWaitingModalVisible]   = useState(true);
    const [InfoModalState, setInfoModalState]             = useState(false);
    const [infoMsg, setInfoMsg]                           = useState("");


    const updateStateFromOrderCard = () => {
        props.navigation.replace("EditOrder");
    }

    const deleteOrder = (id) => {
        deleteInstitutionOrder(getDecryptedUsername(), getDecryptedToken(), id);
    }

    const removeOrderFromView = (id) => {
        let tempOrders = orders;
        var orderID;

        tempOrders.map((order, index) => {
            if(order.id == id) {
                orderID = index;
                return;
            }
        })
        orders.splice(orderID, 1);
    }

 


    useEffect(() => {
        if(data === null){
            fetchInstitutionOrders(getDecryptedUsername(), getDecryptedToken());
        }
        
        if(dataOnce === false && data !== null){
            setOrders(data.data);
            setDataOnce(true);
        }
    });


    const returnOrders = () =>{


        let _orders_ = orders;

        return _orders_.map((order)=>(

            <OrderCard title={order.title} date={()=>{
                let date      = order.expireDate.split("T");
                let temp_date = date[0];
                let tmp_time  = date[1].split("+");


               return temp_date + " " + tmp_time[0];
            }} id={order.id} key={order.id}
             deleteOrder={deleteOrder}
             updateStateFromOrderCard={updateStateFromOrderCard}/>
        ));


    } 


    const fetchInstitutionOrders = async (username, token) => {
        let data          = [];
    
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        var requestOptions = {
            method: 'GET',
            headers: headerData,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/profile/${username}/get_orders/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                let parsed_data = await response.json();
                data["data"] = parsed_data;
                data["status"] = 200;
                data["responseMsg"] = "";
                setData(data);
                setWaitingModalVisible(false);
            }else if(response.status === 401){
                data["responseMsg"] = "      ";
                setData(data);
                setWaitingModalVisible(false);
            }
        }).catch(error => {
            console.log('error', error)
            setInfoMsg('   ');
            setWaitingModalVisible(false);
            setInfoModalState(true);
        });
    }
    
    const deleteInstitutionOrder = async (username, token, id) => {
        let data          = [];
    
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        var requestOptions = {
            method: 'GET',
            headers: headerData,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/profile/${username}/delete_order/${id}/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                setInfoMsg("   ");
                setInfoModalState(true)
                setWaitingModalVisible(false);
                removeOrderFromView(id);
            }else if(response.status === 401){
                setInfoMsg("      ");
                setInfoModalState(true)
                setWaitingModalVisible(false);
            }else if(response.status === 404){
                setInfoMsg("   ");
                setInfoModalState(true)
                setWaitingModalVisible(false);
            }
        }).catch(error => {
            console.log('error', error)
            setInfoMsg('   ');
            setWaitingModalVisible(false);
            setInfoModalState(true);
        });
    }

    return(
        <View>
            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} />

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            <View style={sharedStyles.viewsTitle}>

       

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View>
                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />   
                            <Text style={sharedStyles.viewsTitleText}> </Text>
                            <Image style={{width: 50, height: 50, marginRight: 20}} source={orderIcon} />

                        </View>
                    </View>
                </View>
            </View>

                <View style={{width: '70%', flexGrow: 1, flexDirection: 'column', justifyContent: 'center', alignSelf: 'center', marginTop: 20}}>
                    {/* {getLogsList()} */}
                    {/* <LogCard id={log.id} notification={false} title={log.action} time={log.time} key={index} />  */}
                    
                    {
                        orders !== null ?
                            returnOrders()
                        :
                            null
                    }
                </View>
        </View>
    );

}


