import React, {useState, useEffect} from 'react';
import {View, Text, Image} from 'react-native';

import {getDecryptedToken, getDecryptedUsername, 
    getIdForNotificationCard, setIdForNotificationCard} from '../Globals/index';

import DashboardHeader from './DashboardHeader';
import BackArrow from './BackArrow';
import LogCard from '../Components/LogCard';
import sharedStyles from '../../Styles/Shared/style';

import {connect} from 'react-redux';

import {fetchNotifications, fetchSingleNotification, setNotificationAsViewed} from '../actions/index';

import notifications_icon from '../../assets/icons/notifications.png';

import {useNavigation} from '@react-navigation/native';




export function Notifications(props){

    const navigation = useNavigation();

    const [notifications, setNotifications] = useState([]);
    const [switchToNotificationsDetails, setSwitchToNotificationsDetails] = useState(false);

    const getUpdates = () => {
        if(props.state.data !== undefined){
            setNotifications(props.state.data.notifications);
        }

        if(getIdForNotificationCard() !== null){
            props.setNotificationAsViewed(getDecryptedUsername(), getDecryptedToken(), getIdForNotificationCard());
            props.fetchSingleNotification(getDecryptedUsername(), getDecryptedToken(), getIdForNotificationCard());
            setIdForNotificationCard(null);
        }

        if(switchToNotificationsDetails){
            // setSwitchToNotificationsDetails(false);
            navigation.navigate('NotificationsDetails');
        }
    }


    const updateStateFromLogCard = () => {
        setSwitchToNotificationsDetails(true);
    }


    useEffect(() => {
        getUpdates();
    });

    const getNotificationList = () => {
        let new_notifications = [];
        let skip_indexs       = [];

        //      
        for(let j = 0; j < notifications.length; j++){
            if(notifications[j].seen === false){
                new_notifications.push(notifications[j]);
                skip_indexs.push(j);
            }
        }

        for(let i = 0; i < notifications.length; i++){
            let flagAlreadyAdded = false;

            for(let m = 0; m < skip_indexs.length; m++){
                if(skip_indexs[m] === i){
                    flagAlreadyAdded = true;
                    break;
                }
            }

            if(flagAlreadyAdded === false){
                new_notifications.push(notifications[i]);
            }
        }

        new_notifications.forEach((notification, index) => {
            let data = "";
            let date = "";
            let time = "";

            //copy array


            try {
                data    = notification.time.split("T");
                date    = data[0];
                time    = data[1];


                time    = time.split(".")[0];
                 
                notification.time = date + " " + time;
            }catch(e){
                // console.log(e.message);
            }
        });


       return new_notifications.map((notification, index) => (
            <LogCard id={notification.id} notification={true} title={notification.title} time={notification.time} 
            details={notification.details} seen={notification.seen} key={index} updateStateFromLogCard={updateStateFromLogCard} />
       ));

       


    }

    



    return(
        <View>
            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>

       

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View>

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />   
                            <Text style={sharedStyles.viewsTitleText}></Text>
                            <Image style={{width: 30, height: 34, marginRight: 20}} source={notifications_icon} />

                        </View>
                    </View>
                </View>


            </View>



            
            <View style={{width: '80%', flexGrow: 1, flexDirection: 'column', justifyContent: 'center', alignSelf: 'center', marginTop: 20}}>
                {getNotificationList()}
            </View>
            
        </View>
    );

}


const mapStateToProps = (state) => {
    return {
        state: {
            data: state.InstitutionDetails.data
        }
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchNotifications: (username, token)=>{
            dispatch(fetchNotifications(username, token));
        },

        fetchSingleNotification: (username, token, id) => {
            dispatch(fetchSingleNotification(username, token, id));
        },

        setNotificationAsViewed: (username, token, id) => {
            dispatch(setNotificationAsViewed(username, token, id));
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Notifications);