import React, {useState, useEffect} from 'react';

import { Modal, View, Text, TouchableOpacity, Image, TextInput } from 'react-native';

import { connect } from 'react-redux';

import {confirmAccount} from '../actions/index';

import {WaitingModal} from './WaitingModal';

import {InfoModal} from './InfoModal';

import styles from '../../Styles/Modals/style';
import sharedStyles from '../../Styles/Shared/style';
import unlockIcon from '../../assets/icons/login_unlock2.png';
import numberIcon from '../../assets/icons/signup_number.png';

export function ConfirmationModal(props){

    const [infoModalVisible, setInfoModalVisible]       = useState(false);
    const [waitingModalVisible, setWaitingModalVisible] = useState(false);
    const [infoModalMessage, setInfoModalMessage]       = useState("");
    const [editCode, setEditCode]                       = useState("");

    const updateStates = () => {
        if(props.state.data.AccountConfirmation !== ''){
            setWaitingModalVisible(false);

            if(props.state.data.AccountConfirmation.status === 200){
                if(props.state.data.AccountConfirmation.isAccountActive){
                    setInfoModalMessage(props.state.data.AccountConfirmation.responseMsg);
                    setInfoModalVisible(true);
                }else{
                    setInfoModalMessage(props.state.data.AccountConfirmation.responseMsg);
                    setInfoModalVisible(true);
                }
                props.state.data.AccountConfirmation.status = "";
                props.state.data.AccountConfirmation.responseMsg = "";
            }

            if(props.state.data.AccountConfirmation.status === 400){
                setInfoModalMessage(props.state.data.AccountConfirmation.responseMsg);
                setInfoModalVisible(true);
                props.state.data.AccountConfirmation.status = "";
                props.state.data.AccountConfirmation.responseMsg = "";
            }
            
        }
    }

    const loginInstitution = () => {
        if(editCode.length > 5){
            setWaitingModalVisible(true);
            props.confirmAccount(editCode);
        }else{
            setInfoModalMessage("       .");
            setInfoModalVisible(true);
        }

    }

    useEffect(() => {
        updateStates();
    })

    return(
        <Modal 
            animationType="slide"
            transparent={true}
            visible={true}>


            <View style={styles.centeredView}>

                {
                    waitingModalVisible === true ?
                        <WaitingModal />
                    :
                        null
                }

                {
                    infoModalVisible === true ?
                        <InfoModal message={infoModalMessage} setModalVisible={setInfoModalVisible} doubleClose={true} setParentModalVisible={props.setModalVisible}/>
                    :
                        null
                }


                <View style={styles.backGroundCardWhite}>
                    <View style={styles.backGroundCardWhiteView}>


                        <View>
                            <Text style={styles.titleInfo3}>{"   \n   "}</Text>
                            {/* <TouchableOpacity>
                                <Image source={} />
                            </TouchableOpacity> */}
                        </View>

                        <View style={styles.editCodeCardBackLayer}>
                            <View style={styles.editCodeCardFrontLayer}>

                                <View style={{flexGrow: 1, justifyContent: 'space-between', flexDirection: 'column'}}>
                                    <Text style={styles.titleInfo4}>          </Text>

                                    <View style={{flexGrow: 5, justifyContent: 'space-around', flexDirection: 'row'}}>
                                        <Image source={numberIcon} style={{width: 34, height: 34}} />
                                        <TextInput placeholder="123456" maxLength={6} onChangeText={(value)=>{setEditCode(value);}}
                                        placeholderTextColor={'#ef863085'} style={{textAlign: 'center', color: '#EF8630'}} />
                                    </View>
                                </View>

                            </View>
                        </View>

                        <View style={{width: '50%', alignSelf: 'center'}}>
                            <View style={sharedStyles.buttonBackLayer}>
                                <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{loginInstitution()}}>
                                    <Text style={sharedStyles.textBtn}></Text>
                                    <Image source={unlockIcon} style={sharedStyles.btnIcon} />
                                </TouchableOpacity>
                            </View>
                        </View>



                    </View>
                </View>


            </View>


        </Modal>

    );

}


const mapStateToProps = (state) => {
    return{
        state: {
           data: state
        }
    }
}



const mapDispatchToProps = (dispatch) => {
    return {
        confirmAccount: (editCode) => {
            dispatch(confirmAccount(editCode))
        },
    }
}

export default  connect(mapStateToProps, mapDispatchToProps)(ConfirmationModal);