import React, {useState, useEffect} from 'react';

import {View, Text, Image, TextInput, TouchableOpacity} from "react-native";

import signUpStyles from '../Styles/SignUp/style';
import sharedStyles from '../Styles/Shared/style';
import createOrder from '../Styles/CreateOrder/style';

import {uri, getDecryptedUsername, getDecryptedToken} from './Globals/';

import BackArrow from './Components/BackArrow';
import DashboardHeader from './Components/DashboardHeader';

import { InfoModal } from './Modals/InfoModal';
import { WaitingModal } from './Modals/WaitingModal';
import Map from './Components/Map';

import emailIcon from '../assets/icons/signup_email.png';
import institIcon from '../assets/icons/icons8-organization-100.png';
import numberIcon from '../assets/icons/signup_number.png';
import userIcon from '../assets/icons/login_avatar_3.png';
import domainTypeIcon from '../assets/icons/signup_domain_type.png';
import addressIcon from '../assets/icons/signup_address.png';
import manageAccountIcon from '../assets/icons/manage_account.png';
import descriptionIcon from '../assets/icons/descriptionIcon.png';
import saveIcon from '../assets/icons/save.png';

import style from '../Styles/inputButton.css';



export default function Profile(){

    const [waitingModalVisible, setWaitingModalVisible]   = useState(true);
    const [InfoModalState, setInfoModalState]             = useState(false);
    const [infoMsg, setInfoMsg]                           = useState("");


    const [profileLogo, setProfileLogo]                 = useState();
    const [profileLogoUploaded, setProfileLogoUploaded] = useState(false);
    
    const [logo, setLogo]               = useState("");
    const [name, setName]               = useState("");
    const [username, setUsername]       = useState("");
    const [phone, setPhone]             = useState("");
    const [taxID, setTaxID]             = useState("");
    const [domainType, setDomainType]   = useState("");
    const [address, setAddress]         = useState("");
    const [latitude, setLatitude]       = useState(null);              
    const [longitude, setLongitude]     = useState(null);

    const [email, setEmail]             = useState("");
    const [description, setDescription] = useState("");

    const [dataOnce, setDataOnce]       = useState(false);
    const [data, setData]               = useState(null);
    const [firstRun, setFirstRun]       = useState(false);


    const DefaultLocation = { lat: 15.559544421458101, lng: 32.55455017089844};
    const DefaultZoom     = 10;
    const [defaultLocation, setDefaultLocation]           = useState(DefaultLocation);

    const [location, setLocation]                         = useState(defaultLocation);
    const [zoom, setZoom]                                 = useState(DefaultZoom);


    const firstRunDone = () => {
        setTimeout(() => {
            setFirstRun(true);
        }, 5000)
    }

    useEffect(() => {
        if(firstRun) {
            if(dataOnce === false && data === null){
                fetchInstitutionDetails(getDecryptedUsername(), getDecryptedToken());
                setDataOnce(true);
            }
    
            if(dataOnce === true && data !== null){
                setLogo(data.logo);
                setName(data.name + '( )');
                setUsername(data.username + '( )');
                setPhone(()=>{
                    return data.phone.slice(4)
                });
                setTaxID(data.taxID + '( )');
                setAddress(data.location);
                if(data.map_location !== null){
                    setLatitude(data.map_location.latitude);
                    setLongitude(data.map_location.longtitude);
                    setDefaultLocation({lat: parseFloat(data.map_location.latitude), lng: parseFloat(data.map_location.longtitude)});
                }
                setDomainType(data.domainType);
                setEmail(data.email);
                setDescription(data.description);

                setDataOnce(false);
            }
            
            if(setFirstRun !== true){
                setFirstRun(true);
            }
        }
    });

    const fileSelectedHandler = (file) => {
        setProfileLogoUploaded(true);
        setProfileLogo(file);
    }

    const fetchInstitutionDetails = async (username, token) => {
        let data          = [];

        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        var requestOptions = {
            method: 'GET',
            headers: headerData,
            redirect: 'follow',
        };

        const response = await fetch(`${uri}/api/institution/profile/${username}/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                let parsed_data = await response.json();
                data = parsed_data;
                data["responseMsg"] = "";
                setData(data);
                setWaitingModalVisible(false);
            }else if(response.status === 401){
                setInfoMsg("      ");
                setInfoModalState(true);
                setWaitingModalVisible(false);
            }
        }).catch(error => console.log('error', error));
    }

    const handleChangeZoom = (newZoom) => {
        setZoom(newZoom);
    }

    const handleChangeLocation = (lat, lng) => {
        setLatitude(lat);
        setLongitude(lng);
    }

    const handleSaveChangesBtn = () => {
        setWaitingModalVisible(true);
        if(profileLogoUploaded){
            sendUpdateProfileRequest(getDecryptedUsername(), getDecryptedToken(), profileLogo,
                phone, address, longitude, latitude, description);
        }else{
            sendUpdateProfileRequest(getDecryptedUsername(), getDecryptedToken(), null,
                phone, address, longitude, latitude, description);
        }
    }
    


    const sendUpdateProfileRequest = async (username, token,  profileLogo, phone, address, lng, lat, description) => {
    
        var formdata = new FormData();
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        formdata.append("phone", phone);
        formdata.append("address", address);

        if(profileLogo !== null){
            formdata.append("profileImage", profileLogo, profileLogo.name);
        }

        if(lng === null || lat === null){
            formdata.append("longtitude", "None");
            formdata.append("latitude", "None");
        }else{
            formdata.append("longtitude", lng);
            formdata.append("latitude", lat);
        }

        formdata.append("description", description);

        var requestOptions = {
            method: 'POST',
            headers: headerData,
            body: formdata,
            redirect: 'follow'
        };


      

        const response = await fetch(`${uri}/api/institution/profile/${username}/update_profile/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                setInfoMsg("    !");
                setInfoModalState(true);
                setWaitingModalVisible(false);
            }else if(response.status === 404){
                setInfoMsg("   !");
                setInfoModalState(true);
                setWaitingModalVisible(false);
            }
        }).catch(err => {
            console.log(err);
            setInfoMsg("     !");
            setInfoModalState(true);
            setWaitingModalVisible(false);
        });



    }

    return(
        <View>
            <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} />

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>

       

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View>

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />
                            <Text style={sharedStyles.viewsTitleText}>  </Text>
                            <Image style={{width: 30, height: 30, marginRight: 20}} source={manageAccountIcon} />

                        </View>
                    </View>
                </View>

            </View>
            {
                logo.length > 0 ?
                
                <View style={{flexGrow: 1, flexDirection: 'column', justifyContent: 'center', alignSelf: 'center'}}>
                        <View style={createOrder.ImageBackLayer}>
                            <View style={createOrder.ImageFrontLayer}>
                                <Image source={`${uri}${logo}`} style={createOrder.logo} />
                            </View>
                        </View>
                        <View style={{alignSelf: 'center'}}>
                            <input type="file"  title='  ()'
                                className='input'
                                style={{width: 200}}
                                onChange={(e)=>{fileSelectedHandler(e.target.files[0])}} 
                                accept="image/*" />
                        </View> 
                        <Text style={[createOrder.titleText, {fontSize: 20}]}>      /   'choose file' /    </Text>
                    </View>
                :
                    null
            }
           
            
            {/* body */}
            <View style={[signUpStyles.body, {marginTop: -50}]}>


                {/* body left column */}
                <View style={[signUpStyles.bodyColumn, {padding: 20}]}>

                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <View style={signUpStyles.phoneContainer}>
                                <Text style={{fontSize: 18, color: '#707070'}}>+249    | </Text>
                                <TextInput placeholder="  ( )" placeholderTextColor={'#C7C7CD'} 
                                    style={signUpStyles.textInputPhone} autoCapitalize={'none'}
                                    autoComplete={'off'} numeric={true}  maxLength={9} keyboardType="numeric" 
                                    value={phone} onChangeText={(value)=>setPhone(value)}/>
                            </View>
                            <Image source={numberIcon} style={sharedStyles.btnIcon} />
                        </View>
                    </View>

                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <TextInput placeholder="" placeholderTextColor={'#C7C7CD'} 
                                style={sharedStyles.textInputLarge} value={address} multiline={true}
                                autoCapitalize={'none'} autoComplete={'off'} onChangeText={(value)=>setAddress(value)}/>
                            <Image source={addressIcon} style={sharedStyles.icon} />
                        </View>
                    </View>
                    {/* domain type */}


                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={[sharedStyles.textInputFrontLayer, {height: 100}]}>
                            <TextInput placeholder="  " placeholderTextColor={'#C7C7CD'} 
                                style={[sharedStyles.textInputLarge, {height: '90%'}]} value={domainType}
                                multiline={true} editable={false} onChangeText={(value)=>setAddress(value)}/>
                            <Image source={domainTypeIcon} style={sharedStyles.icon} />
                        </View>
                    </View>


                    {/* email text input */}
                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <Text style={createOrder.titleText}>
                                {
                                    email.length < 1 ?
                                        ' '
                                    :
                                        `${email}`
                                }
                            </Text>
                            <Image source={emailIcon} style={sharedStyles.icon} />
                        </View>
                    </View>

                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={[sharedStyles.textInputFrontLayer, {height: 200}]}>
                            <TextInput placeholder="   " placeholderTextColor={'#C7C7CD'} 
                                style={[sharedStyles.textInputLarge, {height: '90%', textAlign: 'right'}]} 
                                value={description}  multiline={true} onChangeText={(value)=>setDescription(value)}/>
                            <Image source={descriptionIcon} style={sharedStyles.icon} />
                        </View>
                    </View>

                </View>
                {/*  end of body left column */}


                
                {/* body right column */}
                <View style={signUpStyles.bodyColumn}>

                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <TextInput placeholder=" " 
                                placeholderTextColor={'#C7C7CD'} 
                                style={[sharedStyles.textInputLarge, {height: '90%'}]} value={name}
                                multiline={true} editable={false} onChangeText={(value)=>setName(value)}/>
                            <Image source={institIcon} style={sharedStyles.icon} />
                        </View>
                    </View>

                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <TextInput placeholder=" " 
                                placeholderTextColor={'#C7C7CD'} 
                                style={[sharedStyles.textInputLarge, {height: '90%'}]} value={username}
                                multiline={true} editable={false} onChangeText={(value)=>setUsername(value)}/>
                            <Image source={userIcon} style={sharedStyles.icon} />
                        </View>
                    </View>


                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <Text style={createOrder.titleText}>
                                {
                                    taxID.length < 1 ?
                                        '  '
                                    :
                                        `${taxID}`
                                }    
                            </Text>
                            <Image source={numberIcon} style={sharedStyles.btnIcon} />
                        </View>
                    </View>



                    <View style={sharedStyles.textInputBackLayer}>
                        <View style={sharedStyles.textInputFrontLayer}>
                            <Text style={createOrder.titleText}>     </Text>
                            <Map 
                                handleChangeZoom={handleChangeZoom}
                                zoom={zoom}
                                handleChangeLocation={handleChangeLocation}
                                defaultLocation={defaultLocation} />
                            <Text style={[createOrder.titleText, {fontSize: 22, alignText: 'center'}]}>
                                {  
                                    longitude === null || latitude === null ?
                                        ` : ( )  : ( )`
                                    :
                                        ` : ${longitude}  : ${latitude}`
                                }
                            </Text>
                        </View>
                    </View>

                   

                </View>
                {/* end of body right column */}


            </View>
            <View style={{flexGrow: 1, flexDirection: 'row', justifyContent: 'center', alignSelf: 'center', marginTop: -100}}>
                    <View style={sharedStyles.textInputBackLayer}>
                        <TouchableOpacity style={[sharedStyles.textInputFrontLayer, {width: 200}]} onPress={handleSaveChangesBtn}>
                            <Image source={saveIcon} style={createOrder.btnIcon} />
                            <Text style={sharedStyles.textBtn}> </Text>
                        </TouchableOpacity>
                    </View>
            </View>
            {firstRunDone()}
        </View>
    );
}

