import React, {useState, useEffect} from 'react';
import {View, Text, Image, TextInput, TouchableOpacity} from 'react-native';

import {getDecryptedToken, getDecryptedUsername, 
    uri, getLogoUri, getIdForOrderCard, setIdForOrderCard} from '../Globals/index';

import DashboardHeader from '../Components/DashboardHeader';
import BackArrow from '../Components/BackArrow';
import LogCard from '../Components/LogCard';
import sharedStyles from '../../Styles/Shared/style';

import {connect} from 'react-redux';

import createOrder from '../../Styles/CreateOrder/style';

import {useNavigation} from '@react-navigation/native';

import orderIcon from '../../assets/icons/order.png';
import numberIcon from '../../assets/icons/signup_number.png';
import posterIcon from '../../assets/icons/poster.png';
import dateIcon from '../../assets/icons/signup_date.png';

import "react-datepicker/dist/react-datepicker.css";

import DatePicker from "react-datepicker";

import saveIcon from '../../assets/icons/save.png';
import publishIcon from '../../assets/icons/publish.png';


import {InfoModal} from '../Modals/InfoModal';
import {WaitingModal} from '../Modals/WaitingModal';


function EditOrder (props){




    const [id, setID]                                     = useState(getIdForOrderCard);
    const [data, setData]                                 = useState(null);
    const [dataOnce, setDataOnce]                         = useState(false);
    const [posterUploaded, setPosterUploaded]             = useState(false)
    const [allowSubmits, setAllowSubmits]                 = useState(false);

    const [waitingModalVisible, setWaitingModalVisible]   = useState(true);
    const [InfoModalState, setInfoModalState]             = useState(false);
    const [infoMsg, setInfoMsg]                           = useState("");

    const [logo, setLogo]                                 = useState(getLogoUri);
    const [title, setTitle]                               = useState("");
    const [imagePoster, setImagePoster]                   = useState(null);
    const [poster, setPoster]                             = useState();
    const [description, setDescription]                   = useState("");
    const [expireDate, setExpireDate]                     = useState("");
    const [expireDateHolder, setExpireDateHolder]         = useState();
    const [availableSeats, setAvailableSeats]             = useState("");
    const [maxSubmits, setMaxSubmits]                     = useState("");

    const update_and_publish = () => {
        if(title.length > 0 && description.length > 0 && availableSeats > 0 && maxSubmits > 0){
            try{
                if(posterUploaded === true){
                    setWaitingModalVisible(true);
                    sendUpdatePublishOrderRequest(getDecryptedUsername(), getDecryptedToken(), id, title, poster, description, expireDate, availableSeats, maxSubmits);
                }else{
                    setWaitingModalVisible(true);
                    sendUpdatePublishOrderRequest(getDecryptedUsername(), getDecryptedToken(), id, title, null, description, expireDate, availableSeats, maxSubmits);
                }
            }catch(e){
                console.error(e.message);
            }
        }else{
            setInfoModalState(true);
            setInfoMsg("     !");
        }
    }

    const save_order = () => {
        if(title.length > 0 && description.length > 0 && availableSeats > 0 && maxSubmits > 0){
            try{
                if(posterUploaded === true){
                    setWaitingModalVisible(true);
                    sendUpdateOrderRequest(getDecryptedUsername(), getDecryptedToken(), id, title, poster, description, expireDate, availableSeats, maxSubmits);
                }else{
                    setWaitingModalVisible(true);
                    sendUpdateOrderRequest(getDecryptedUsername(), getDecryptedToken(), id, title, null, description, expireDate, availableSeats, maxSubmits);
                }
            }catch(e){
                console.error(e.message);
            }
        }else{
            setInfoModalState(true);
            setInfoMsg("     !");
        }
    }



    useEffect(() => {
        if(data === null){
            fetchInstitutionOrder(getDecryptedUsername(), getDecryptedToken(), id);
        }

        // console.log(props.state);
        if(props.state !== ''){
            // console.log(props.state);
        }

        if(dataOnce === false && data !== null){
            setTitle(data.data.title);
            if(data.data.poster !== null){
                setImagePoster(data.data.poster);
            }
            setDescription(data.data.orderDetails);
            setAvailableSeats(data.data.availableSeats);
            setMaxSubmits(data.data.max_submits);

            let array = [];
            var _date_, _time_, fullDate;

            array    = data.data.expireDate.toString().split("T");
            _date_   = array[0];
            _time_   = array[1].split(".");
            fullDate = _date_ + " " + _time_[0];

            setExpireDate(fullDate);
            setAllowSubmits(data.data.allow_submits);

            setDataOnce(true);
            setData(null);
        }

        if(data !== null) {
            if (data.responseMsg !== '') {
                setInfoMsg(data.responseMsg);
                setInfoModalState(true);
                setData(null);
            }
        }

    });


    const handleDate = (date) => {
        let array = [];
        let fullDate = "";
        let _date_ = "";
        let _time_ = [];
        // newDate = date.getUTCFullYear() + "-" + date.getUTCMonth() + "-" + date.getUTCDay();
        // date.getFullYear
        // newDate = newDate + " " + date.getUTCHours() + ":" + date.getUTCMinutes() + ":" + date.getUTCSeconds()

        // console.log(newDate);

        try{
            fullDate = date.toJSON();

            array    = fullDate.toString().split("T");
            _date_   = array[0];



            _time_   = array[1].split(".");


            fullDate = _date_ + " " + _time_[0] + "+02:00";
        }catch(e){

        }

        // console.log(newDate);
        
        setExpireDate(fullDate);
        setExpireDateHolder(date);

        // console.log(" iso time -> " + date.parseISO());

    }

    const checkIfItsAnumber = (value) => {
        if(isNaN(value)){
            return false;
        }
        return  true;
    }

    const fileSelectedHandler = (file) => {
        setPosterUploaded(true);
        setPoster(file);
    }

    const fetchInstitutionOrder = async (username, token, id) => { 
        let data          = [];
    
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        var requestOptions = {
            method: 'GET',
            headers: headerData,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/profile/${username}/get_order/${id}/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                let parsed_data = await response.json();
                data["data"] = parsed_data;
                data["status"] = 200;
                data["responseMsg"] = "";
                setWaitingModalVisible(false);
                setData(data);
            }else if(response.status === 401){
                data["status"] = 401;
                data["responseMsg"] = "      ";
                setWaitingModalVisible(false);
                setData(data);
            }
        }).catch(error => console.log('error', error));
    }


    const sendUpdateOrderRequest =  async(username, token, orderID, title, poster=null, description, expireDate, availableSeats, maxSubmits) => {
        let data          = [];
      
        var formdata = new FormData();
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        formdata.append("username", username);
        formdata.append("title", title);

        if(poster !== null){
            formdata.append("poster", poster, poster.name);
        }

        formdata.append("orderDetails", description);
        formdata.append("expireDate", expireDate);
        formdata.append("availableSeats", availableSeats);
        formdata.append("max_submits", maxSubmits);

        var requestOptions = {
            method: 'POST',
            headers: headerData,
            body: formdata,
            redirect: 'follow'
        };


        try{

            const controller = new AbortController();
            const timeout    = 6000;
            const id         = setTimeout(() => {
                controller.abort();
                data["status"]      = 408;
                data["responseMsg"] = "  !";
                // setWaitingModalVisible(false);
                setData(data);
            }, timeout);

            const response = await fetch(`${uri}/api/institution/profile/${username}/update_order/${orderID}/`, requestOptions, {signal: controller.signal})
            .then( async (response) => {
                if(response.status === 200){
                    data["status"]      = 200;
                    data["responseMsg"] = "    !";
                    setWaitingModalVisible(false);
                    setData(data);
                }else if(response.status === 400) {
                    let parsed          = response.json();
                    data["status"]      = 400;
                    data["responseMsg"] = parsed;
                    setWaitingModalVisible(false);
                    setData(data);
                }else if(response.status === 404){
                    data["status"]      = 404;
                    data["responseMsg"] = "   !";
                    setWaitingModalVisible(false);
                    setData(data);
                }
            }).catch(err => {console.log(err)});

            clearTimeout(id);

        }catch(error){
            console.log('error', error.message);
        }
    
    }

    const sendUpdatePublishOrderRequest = async (username, token, orderID, title, poster=null, description, expireDate, availableSeats, maxSubmits) => {
        let data          = [];
      
        var formdata = new FormData();
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        formdata.append("username", username);
        formdata.append("title", title);

        if(poster !== null){
            formdata.append("poster", poster, poster.name);
        }

        formdata.append("orderDetails", description);
        formdata.append("expireDate", expireDate);
        formdata.append("availableSeats", availableSeats);
        formdata.append("max_submits", maxSubmits);

        var requestOptions = {
            method: 'POST',
            headers: headerData,
            body: formdata,
            redirect: 'follow'
        };


        try{

            const controller = new AbortController();
            const timeout    = 6000;
            const id         = setTimeout(() => {
                controller.abort();
                data["status"]      = 408;
                data["responseMsg"] = "  !";
                // setWaitingModalVisible(false);
                setData(data);
            }, timeout);

            const response = await fetch(`${uri}/api/institution/profile/${username}/publish_order/${orderID}/`, requestOptions, {signal: controller.signal})
            .then( async (response) => {
                if(response.status === 200){
                    data["status"]      = 200;
                    data["responseMsg"] = "     !";
                    setWaitingModalVisible(false);
                    setData(data);
                }else if(response.status === 400) {
                    let parsed          = response.json();
                    data["status"]      = 400;
                    data["responseMsg"] = parsed;
                    setWaitingModalVisible(false);
                    setData(data);
                }else if(response.status === 404){
                    data["status"]      = 404;
                    data["responseMsg"] = "   !";
                    setWaitingModalVisible(false);
                    setData(data);
                }
            }).catch(err => {console.log(err)});

            clearTimeout(id);

        }catch(error){
            console.log('error', error.message);
        }
    
    }

    return(
        <View>


            <InfoModal message={infoMsg} modalVisible={InfoModalState} 
            setModalVisible={setInfoModalState} />

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            <View style={{width: '100%'}}>
                <DashboardHeader />
            </View>

            <View style={sharedStyles.viewsTitle}>

        

                <View style={{width: 120, marginRight: 20}}>
                    <BackArrow />
                </View>

                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{width: "100%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>

                            <Image style={{width: 30, height: 30}} />   
                            <Text style={sharedStyles.viewsTitleText}> </Text>
                            <Image style={{width: 50, height: 50, marginRight: 20}} source={orderIcon} />

                        </View>
                    </View>
                </View>


            </View>


            <View style={{width: '80%', flexGrow: 1, flexDirection: 'column', justifyContent: 'center', alignSelf: 'center', marginTop: 20}}>
                
                <View style={sharedStyles.textInputBackLayer}>
                    <View style={sharedStyles.textInputFrontLayer}>

                        <View style={createOrder.ImageBackLayer}>
                            <View style={createOrder.ImageFrontLayer}>
                                <Image source={`${uri}${logo}`} style={createOrder.logo} />
                            </View>
                        </View>

                        <View style={{marginTop: 40, alignItems: 'center'}}>

                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                    <Text placeholder=' ' style={createOrder.titleText}>{title} (  )</Text>
                                </View>
                            </View>

                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                   <Image source={`${uri}${imagePoster}`} style={createOrder.imagePoster} />
                                </View>
                            </View>

                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                    <Image source={posterIcon} style={sharedStyles.btnIcon} />
                                    <Text style={createOrder.titleText}>{`   () () `}</Text>
                                    <input id="poster" type="file"  title='  ()' onChange={(e)=>{fileSelectedHandler(e.target.files[0])}} accept="image/*" style={{alignSelf: 'center'}} />
                                </View>
                            </View>
                            

                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                    <TextInput placeholder={`   (  )  \n    (        )`} placeholderTextColor={'#C7C7CD'} 
                                        multiline={true} style={sharedStyles.textInputLargeWithoutIconMultiline}
                                        value={description} onChangeText={(value)=>{setDescription(value)}} />
                                </View>
                            </View>


                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                    <TextInput placeholder='   ' 
                                    placeholderTextColor={'#C7C7CD'}  style={sharedStyles.textInputLargeWithoutIcon} 
                                    value={availableSeats}
                                    onChangeText={(value)=>{if(checkIfItsAnumber(value)){setAvailableSeats(value)}}} />
                                    <Image source={numberIcon} style={sharedStyles.btnIcon} />
                                </View>
                            </View>

                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                    <TextInput placeholder='    (     )' 
                                    placeholderTextColor={'#C7C7CD'}  style={sharedStyles.textInputLargeWithoutIcon}
                                    value={maxSubmits} 
                                    onChangeText={(value)=>{if(checkIfItsAnumber(value)){setMaxSubmits(value)}}} />
                                    <Image source={numberIcon} style={sharedStyles.btnIcon} />
                                </View>
                            </View>

                            <View style={createOrder.textInputBackLayer}>
                                <View style={createOrder.textInputFrontLayer}>
                                    <View style={{alignSelf: 'center', alignItems: 'center'}}>
                                        <Text style={createOrder.titleText}>     ( {expireDate} ) </Text>
                                        <DatePicker placeholderText='    ' placeholderTextColor={'#C7C7CD'} 
                                        dateFormat={'yyyy-MM-dd hh:mm:ss'} selected={expireDateHolder} onChange={(date) => {handleDate(date)}} />
                                    </View>
                                    <Image source={dateIcon} style={sharedStyles.btnIcon} />
                                </View>
                            </View>

                            
                            {
                                allowSubmits === false ?

                                    <View style={{marginTop: 10, width: "30%"}}>

                                        <View style={sharedStyles.buttonBackLayer}>
                                        {/* onPress={this.create_order} */}
                                            <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{update_and_publish()}}>
                                                <Text style={sharedStyles.textBtn}>   </Text>
                                                <Image style={sharedStyles.btnIcon24} source={publishIcon} />
                                            </TouchableOpacity>
                                        </View>

                                    </View>

                                :
                                    null
                            }

                            <View style={{marginTop: 10,  width: "30%"}}>

                                <View style={sharedStyles.buttonBackLayer}>
                                {/* onPress={save_order} */}
                                    <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{save_order()}}>
                                            <Text style={sharedStyles.textBtn}>   </Text>
                                            <Image style={createOrder.btnIcon} source={saveIcon} />
                                    </TouchableOpacity>
                                </View>

                            </View>
                        
                            
                        </View>

                    </View>
                </View>

            </View>
            
        </View>
    );

}




export default EditOrder;
