import React, {useState, useEffect} from 'react';
import {View, Text, Image, TouchableOpacity, TextInput} from 'react-native';

import Header from './Components/Header';
import ForgotFooter from './Components/ForgotFooter';
import BackArrow from './Components/BackArrow';


import numberIcon from '../assets/icons/signup_number.png';
import keyIcon from '../assets/icons/login_key2.png';
import showPasswordIcon from '../assets/icons/signup_opendEye.png';
import hidePasswordIcon from '../assets/icons/signup_closedEye.png';

import forgotStyles from '../Styles/ForgotPassword/style';
import sharedStyles from '../Styles/Shared/style';

import { InfoModal } from './Modals/InfoModal';
import { WaitingModal } from './Modals/WaitingModal';

import { sendForgotPassword2Action } from './actions/';

import { connect } from 'react-redux';


export function ForgotPassword2 (props) {

    const [showPassword, setShowPassword]               = useState(false);

    const [editCode, setEditCode]                       = useState("");

    const [password1, setPassword1]                     = useState("");
    const [password2, setPassword2]                     = useState("");

    const [infoModalVisible, setInfoModalVisible]       = useState(false);
    const [waitingModalVisible, setWaitingModalVisible] = useState(false);
    const [infoModalMessage, setInfoModalMessage]       = useState("");


    const updateStates = () => {


        if(props.state !== undefined) {
            if(props.state.data !== ''){

                // console.log(props.state.data);
                if(props.state.data.status === 200){
                    setInfoModalMessage(props.state.data.responseMsg);
                    setInfoModalVisible(true);
                }

                setWaitingModalVisible(false);
                props.state.data = '';
            }
        }

    }

    const resetPassword = () => {
        if(editCode.length < 6){
            setInfoModalMessage("       !");
            setInfoModalVisible(true);
            return;
        }else if(password1.length < 8){
            setInfoModalMessage("         !");
            setInfoModalVisible(true);
            return;
        }else if(password1 !== password2){
            setInfoModalMessage("  !");
            setInfoModalVisible(true);
            return;
        }

        setWaitingModalVisible(true);
        props.sendForgotPassword2Action(editCode, password1);
    }

    useEffect(() => {
        updateStates();
    })

    return(

        <View style={forgotStyles.view}>
            {
                infoModalVisible === true ?
                    <InfoModal message={infoModalMessage} setModalVisible={setInfoModalVisible} 
                    navigateTo={true} distinationAfterClose={'Login'}/>
                :
                    null
            }

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            {/* header */}
            <View style={{width: '100%'}}>
                <Header />
            </View>


            {/* card */}
            <View style={forgotStyles.cardBackLayer}>
                <View style={forgotStyles.cardFrontLayer}>


                    {/* card title */}
                    <Text style={sharedStyles.titleText}>   </Text>

                    {/* card body */}
                    <View style={forgotStyles.body}>

                            {/* password reset code */}
                            <View style={{width: '60%'}}>
                                <View style={sharedStyles.textInputBackLayer}>
                                    <View style={sharedStyles.textInputFrontLayer}>
                                        <Text style={sharedStyles.textInputTitleTextPasswordReset}>
                                                        
                                        </Text>

                                        <View style={{marginTop: 5}}>
                                            <TextInput placeholder="123456" placeholderTextColor={'#fad8bd'}  style={sharedStyles.textInputTitleLarge} 
                                                value={editCode} onChangeText={(value)=>{setEditCode(value)}}  />
                                            <Image source={numberIcon} style={sharedStyles.textInputTitlebtnIcon}  />
                                        </View>
                                    </View>
                                </View>
                                
                            </View>

                                {/* password text input */}
                                <View style={sharedStyles.textInputBackLayer}>
                                <View style={sharedStyles.textInputFrontLayer}>

                                    {
                                        showPassword ?
                                            <TouchableOpacity onPress={()=>{setShowPassword(!showPassword)}}>
                                                <Image source={showPasswordIcon} style={sharedStyles.iconRight} />
                                            </TouchableOpacity>
                                        :
                                            <TouchableOpacity onPress={()=>{setShowPassword(!showPassword)}}>
                                                <Image source={hidePasswordIcon} style={sharedStyles.iconRight} />
                                            </TouchableOpacity>
                                    }
                                    
                                    <TextInput placeholder="  " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                        value={password1} onChangeText={(value) => {setPassword1(value)}} secureTextEntry={!showPassword} autoComplete={'off'} />
                                    <Image source={keyIcon} style={sharedStyles.icon} />
                                </View>
                            </View>
                            <View style={sharedStyles.textInputBackLayer}>
                                <View style={sharedStyles.textInputFrontLayer}>
                                    <TextInput placeholder="    " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                        value={password2} onChangeText={(value) => {setPassword2(value)}} secureTextEntry={!showPassword} autoComplete={'off'} />
                                    <Image source={keyIcon} style={sharedStyles.icon} />
                                </View>
                            </View>

                            

                            <View style={{width: '45%'}}>
                                <View style={{width: '65%', marginTop: 12, alignSelf: 'center'}}>
                                    <View style={sharedStyles.buttonBackLayer}>
                                        <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{resetPassword();}} >
                                            <Text style={sharedStyles.textBtn}>    </Text>
                                            <Image source={keyIcon} style={sharedStyles.btnIcon} />
                                        </TouchableOpacity>
                                    </View>
                                </View>
                            </View>

                    </View>

                </View>
            </View>


            <View style={{width: 120}}>
                <BackArrow />
            </View>

            {/* footer */}

            <View style={{width: '100%'}}>
                <ForgotFooter />
            </View>


        </View>


    );
}


const mapStateToProps = (state) => {
    return {
        state: {
            data: state.ForgotPassword2ActionResponse
        }
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        sendForgotPassword2Action: (editCode, new_password) => {
            dispatch(sendForgotPassword2Action(editCode, new_password))
        }
    }
}


export default  connect(mapStateToProps, mapDispatchToProps)(ForgotPassword2);