import React, {useState, useEffect} from 'react';
import {View, Text, Image, TouchableOpacity, TextInput} from 'react-native';
import {useNavigation} from '@react-navigation/native';

import Header from './Components/Header';
import ForgotFooter from './Components/ForgotFooter';
import BackArrow from './Components/BackArrow';

import emailIcon from '../assets/icons/signup_email.png';
import numberIcon from '../assets/icons/signup_number.png';
import forgotPassIcon from '../assets/icons/login_forgot_password2.png';

import forgotStyles from '../Styles/ForgotPassword/style';
import sharedStyles from '../Styles/Shared/style';


import { connect } from 'react-redux';

import { sendForgotPassword1Action } from './actions/';

import {InfoModal} from './Modals/InfoModal';
import {WaitingModal} from './Modals/WaitingModal';




export function ForgotPassword1(props){

    const navigation = useNavigation();

    const [email, setEmail]                             = useState("");
    const [taxID, setTaxID]                             = useState("");

    const [waitingModalVisible, setWaitingModalVisible] = useState(false);
    const [infoModalVisible, setInfoModalVisible]       = useState(false);
    const [infoModalMessage, setInfoModalMessage]       = useState("");

    const sendEmailCode = () => {
        if(!email.match("[a-zA-Z0-9._][@][a-zA-Z._]+[.][a-zA-Z]+")){
            setInfoModalMessage("    ! ");
            setInfoModalVisible(true);
            return;
        }else if (taxID.length < 1){
            setInfoModalMessage("    ");
            setInfoModalVisible(true);
            return;
        }
        setWaitingModalVisible(true);
        props.sendForgotPassword1Action(email, taxID);
    }

    const updateStates = () => {

        if(props.state !== undefined){
            if(props.state.data !== ''){
                setWaitingModalVisible(false);
                if(props.state.data.status === 200){
                    navigation.navigate('ForgotPassword Step 2');
                }else if(props.state.data.status === 422){
                    setInfoModalMessage(props.state.data.responseMsg);
                    setInfoModalVisible(true);
                }else if(props.state.data.status === 404){
                    setInfoModalMessage(props.state.data.responseMsg);
                    setInfoModalVisible(true);
                }
                props.state.data = '';
            }
        }

    }

    useEffect(() => {
        updateStates();
    })

    return(

        <View style={forgotStyles.view}>

            {
                waitingModalVisible === true ?
                    <WaitingModal />
                :
                    null
            }

            {
                infoModalVisible === true ?
                    <InfoModal message={infoModalMessage} setModalVisible={setInfoModalVisible} />
                :
                    null
            }

            {/* header */}
            <View style={{width: '100%'}}>
                <Header />
            </View>


            {/* card */}
            <View style={forgotStyles.cardBackLayer}>
                <View style={forgotStyles.cardFrontLayer}>


                    {/* card title */}
                    <Text style={sharedStyles.titleText}>   </Text>

                    {/* card body */}
                    <View style={forgotStyles.body}>

                            {/* email text input */}
                            <View style={sharedStyles.textInputBackLayer}>
                                <View style={sharedStyles.textInputFrontLayer}>
                                    <TextInput placeholder=" " textContentType={'emailAddress'} value={email} placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                        onChangeText={(value)=>{setEmail(value)}} autoCapitalize={'none'} autoComplete={'off'} />
                                    <Image source={emailIcon} style={sharedStyles.icon} />
                                </View>
                            </View>


                            <View style={sharedStyles.textInputBackLayer}>
                                <View style={sharedStyles.textInputFrontLayer}>
                                    <TextInput placeholder="  " placeholderTextColor={'#C7C7CD'} style={sharedStyles.textInputLarge} 
                                        value={taxID} onChangeText={(value)=>{setTaxID(value)}} autoCapitalize={'none'} autoComplete={'off'} />
                                    <Image source={numberIcon} style={sharedStyles.btnIcon} />
                                </View>
                            </View>

                            <View style={{width: '45%'}}>
                                <View style={{width: '65%', marginTop: 12, alignSelf: 'center'}}>
                                    <View style={sharedStyles.buttonBackLayer}>
                                        <TouchableOpacity style={sharedStyles.buttonFrontLayer} onPress={()=>{sendEmailCode()}}>
                                            <Text style={sharedStyles.textBtn}>  </Text>
                                            <Image source={forgotPassIcon} style={sharedStyles.btnIcon} />
                                        </TouchableOpacity>
                                    </View>
                                </View>
                            </View>

                    </View>

                </View>

            </View>
            <View style={{width: 120}}>
                <BackArrow />
            </View>
                


            {/* footer */}

            <View style={{width: '100%'}}>
                <ForgotFooter />
            </View>


        </View>


    );

}



const mapStateToProps = (state) => {
    return {
        state: {
            data: state.ForgotPassword1ActionResponse
        }
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        sendForgotPassword1Action: (email, taxID) => {
            dispatch(sendForgotPassword1Action(email, taxID))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ForgotPassword1);