import React, {useState} from 'react';
import {View, Text, TouchableOpacity, Image} from 'react-native';
import sharedStyles from '../../Styles/Shared/style';

import notification_info from '../../assets/icons/notification_info_circle.png';

import {useNavigation} from '@react-navigation/native';

import { setIdForOrderCard } from '../Globals/index';
import createOrder from '../../Styles/CreateOrder/style';
import pencilIcon from '../../assets/icons/pencil.png';
import deleteIcon from '../../assets/icons/deleteIcon.png';





export default function OrderCard(props){


    const navigation        = useNavigation();

    const [date, setDate]   = useState(props.date);
    const [title, setTitle] = useState(props.title);
    const [id, setID]       = useState();


    const goToEditOrder = () => {
        setIdForOrderCard(props.id);
        props.updateStateFromOrderCard();
    }

    const deleteOrder = () => {
        props.deleteOrder(props.id);
    }


    return(
        <View style={{marginTop: 5}}>
            <View style={sharedStyles.textInputBackLayer}>
                {
                    <View style={sharedStyles.textInputFrontLayer}>
                        <View style={{flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between', padding: 20}}>
                            {/* <Text style={sharedStyles.logCardTitleText}>{props.time}</Text>
                            <Text style={sharedStyles.logCardTitleText}>{props.title}</Text> */}
                            <Text style={[createOrder.cardTitle, {marginTop: 100, color: '#707070'}]}>{date}</Text>
                            <Text style={[createOrder.cardTitle, {fontSize: 30}]}>{title}</Text>

                            <View style={{flexGrow: 0.0125, flexDirection: 'column', justifyContent: 'space-between'}}>
                                <View style={createOrder.OrdersEditButtonBackLayer}>
                                    <TouchableOpacity style={[sharedStyles.textInputFrontLayer, {alignItems: 'center'}]} onPress={goToEditOrder} >
                                        <Image style={{width: 30, height: 30}} source={pencilIcon} />
                                        <Text style={createOrder.cardButtonTitleText}>{' '}</Text>
                                    </TouchableOpacity>
                                </View>
                                <View style={createOrder.OrdersEditButtonBackLayer}>
                                    <TouchableOpacity style={[sharedStyles.textInputFrontLayer, {alignItems: 'center'}]} onPress={deleteOrder} >
                                        <Image style={{width: 30, height: 30}} source={deleteIcon} />
                                        <Text style={createOrder.cardButtonTitleText}>{' '}</Text>
                                    </TouchableOpacity>
                                </View>
                            </View>

                        </View>
                    </View>
                }
            </View>
        </View>
    );
}