import React from 'react';
import {View, Text, TouchableOpacity, Image} from 'react-native';
import sharedStyles from '../../Styles/Shared/style';

import notification_info from '../../assets/icons/notification_info_circle.png';

import {useNavigation} from '@react-navigation/native';

import { setIdForNotificationCard } from '../Globals/index';




export default function LogCard(props){


    const navigation = useNavigation();

    const goToNotificationDetails = () => {
        setIdForNotificationCard(props.id);
        props.updateStateFromLogCard();
    }



    return(
        <View style={{marginTop: 5}}>
            <View style={sharedStyles.textInputBackLayer}>

                {
                    props.notification === true ?
                        <TouchableOpacity style={sharedStyles.textInputFrontLayer} onPress={()=>{goToNotificationDetails()}}>
                            <View style={{flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>
                                <Text style={sharedStyles.logCardTitleText}>{props.time}</Text>
                                <Text style={sharedStyles.logCardTitleText}>{props.title}</Text>
                                {
                                    props.seen === false ?
                                        <Image style={{width: 30, height: 30, marginRight: 20}} source={notification_info} />
                                    :
                                        <Image />
                                }
                            </View>
                        </TouchableOpacity>
                    :
                        <View style={sharedStyles.textInputFrontLayer}>
                            <View style={{flexGrow: 1, flexDirection: 'row', justifyContent: 'space-between'}}>
                                <Text style={sharedStyles.logCardTitleText}>{props.time}</Text>
                                <Text style={sharedStyles.logCardTitleText}>{props.title}</Text>
                                <Text></Text>
                            </View>
                        </View>
                }
            </View>
        </View>
    );
}